.header{
    display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 10px 0;
   position: sticky;
   top: 0;
   z-index: 100;
   background-color: white;
}
.header__first{
    margin-left: 15px;
}
.header__logo{
    height: 25px;
    width: 80px;
    object-fit: contain;
    margin-left: 20px;
}
.header__input{
    display: flex;
    align-items: center; 
    width: 40%;
    border: 1px solid black; 
}
.header__icons{
    display: flex;
    align-items: center;
    margin: 0 20px;
}
.header__input > input{
    flex: 1;
    border: none;
    outline: none;
}
.header__inputButton{
    align-items: center;
    width: 10px;
    background-color: #fafafa;
    padding: 0 20px;
    border-left: 1px solid gray;
    color: gray;
    cursor: pointer;
}
.header__inputButton:hover{
    color: gray;

}
.header__icon{
    margin-left: 10px;
}
