.videoRow{
    display: flex;
    margin-bottom: 30px;
    max-width: 700px;
}
.videoRow > img{
    object-fit: contain;
    width: 246px;
    height: 138px;
}
.videoRow__text{
    margin-left: 14px;
}
.videoRow__text > h3{
    font-size:small;
}
.videoRow__info{
    margin-top: 10px;
    font-size: 14px;
    color: lightgray;
}
.videoRow__description{
    margin-top: 20px;
    font-size: 12px;
    color: lightgray;
}
.videoRow__text > p {
    color: #606860;
    font-size: small !important;
}
.videoRow__subsNumber{
    font-weight: bolder;
    color: rgb(84, 157, 211);
}
.videoRow__subs{
    background-color: lightgray;
}